body {
  height: 98vh;
  margin: 0 0 2vh;
}

.search-form {
  height: 10%;
  background-color: #add8e6;
  justify-content: center;
  align-items: center;
  gap: 2vh;
  display: flex;
}

input {
  width: 40vw;
  padding: 4px;
  font-size: 16px;
}

.submit-btn {
  height: 29px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.photo-card {
  border: 1px solid #d3d3d3;
  border-top: none;
  border-radius: 5%;
  margin-bottom: 15px;
}

img {
  object-fit: cover;
  width: 370px;
  height: 230px;
  border-top-left-radius: 5%;
  border-top-right-radius: 5%;
}

.info {
  grid-template-columns: 1fr 1fr 1fr 1fr;
  display: grid;
}

.info-item {
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  display: flex;
}

